import "../style/text.scss";
import "../style/welcome-page.scss";
import "../global/FormButton";

import { useNavigate } from "react-router-dom";

function WelcomePage() {
  const navigate = useNavigate();
  const handleRedirectToLogin = () => {
    console.log("click");
    navigate("/login-form");
  };
  const handleRedirectToRegister = () => {
    console.log("click");
    navigate("/register-form");
  };

  return (
    <div className="welcome-page">
      <div className="theme">Witaj strudzony wdrowcze! </div>
      <div className="text-area-wrapper">
        Strona na ktrej jeste suy do katalogowania posiadanych kosmetykw.
        Do dalszego korzystania ze strony niezbdne jest zalogowanie si.
      </div>

      <button className="form-button" onClick={handleRedirectToLogin}>
        Zaloguj si
      </button>
      <button className="form-button" onClick={handleRedirectToRegister}>
        Zarejestruj si
      </button>
    </div>
  );
}
export default WelcomePage;
