import "../style/text.scss";

function FAQ() {
  return (
    <div className="page">
      <div className="theme">Dodaj produkt</div>

      <div className="text-area-wrapper">
        Dodaj produkt jest podstron umoliwiajc dodanie nowego kosmetyku na
        list produktw uytkownika. W tym celu niezbdne jest uzupenienie
        nastpujcych pl:
        <ol>
          <li>
            Producent - pole przeznaczone na nazw przedsibiorstwa
            wytwarzajcego dany produkt.
          </li>
          <li>
            Kategoria gwna - wybrane z listy pole okrelajcy rodzaj produkty
          </li>
          <li>
            Podkategoria - pole moliwe do wybrania po ustawieniu kategorii
            gwnej.
          </li>
          <li>
            Nazwa produktu - pole przeznaczone na skrcon nazwe produktu. Do
            uszczeglnienia lub dodatkowych informacji suy opis produktu.
          </li>
        </ol>
      </div>

      <div className="text-area-wrapper">
        PAO (Period After Opening)  jest to midzynarodowe oznaczenie, ktre
        wskazuje na przydatno produktu po otwarciu opakowania.
      </div>

      <div className="text-area-wrapper">
        Wrd dodatkowych pl znajduj si:
        <ol>
          <li>
            Ilo, Jednostka, Cena - pozwalaj na automatyczne obliczanie kwoty
            za jednostk danego produktu. Znajduje si ona w zakadce z list
            produktw po rozwiniciu szczegowych informacji danego produktu.
          </li>
          <li>
            Pola zwizane z dat - umoliwiajc filtrowanie produktw poprzez
            nadanie im statusu: w uyciu, zuyte, nieuywane.
          </li>
        </ol>
      </div>
      <div className="theme">Lista produktw</div>
      <div className="text-area-wrapper">
        Lista produktw jest zakadk pozwalajc uytkownikowi na podgld,
        filtrowanie, edycje oraz usuwanie dodanych do bazy danych produktw.
      </div>
      <div className="text-area-wrapper">
        Czas do zuycia jest funkcjonalnoci, ktra porwnujc ze sob dat
        wanoci oraz PAO (po wprowadzeniu daty otwarcia), wywietla najkrtszy
        moliwy czas na bezpieczne uywanie produktu.
      </div>
    </div>
  );
}
export default FAQ;
