import "../style/global/action-button.scss";
import iconexpand from "../images/icon-expand.svg";
import { Tooltip } from "react-tooltip";
import { useState } from "react";

function ExpandButton(props) {
  const [active, setActive] = useState(false);
  const handleActiveClick = () => {
    setActive(!active); //by klasa zmieniaa si przy odklikniciu na przeciwn. Zostawienie na true dziaao by tylko na jedno kliknicie.
    props.handleClick();
  };

  return (
    <button
      onClick={handleActiveClick}
      className="action-button"
      disabled={props.disabled}
    >
      <a className="tooltip-expand-button" data-tooltip-content={props.tooltip}>
        <img
          alt="icon-expand"
          className={`expand-icon ${active ? "active" : ""}`}
          //umieszczone s tu dwie klasy: expand-icon oraz klasa activ pozwalajca na zarzdzanie stanem przycisku
          src={iconexpand}
        ></img>
        {active}
      </a>

      <Tooltip anchorSelect=".tooltip-expand-button" />

      {props.title}
    </button>
  );
}
export default ExpandButton;
